				"html": "<p>Wow, writing a weekly update will be challenging but valuable. The rain in Southern California has lingered which great for water levels and local waterfalls up above Pasadenaâ€”Iâ€™ve enjoyed it thoroughly. This environmental change has switched my sport focus a bitâ€”climbing more and surfing less. Beyond that, JavaScript MVC discovery has led to new open source projects to make and a few projects to contribute to as well. </p>\n<h2>Mueller Tunnel Continues to Enchant</h2>\n<html><head></head><body><figure>\n  \n</figure></body></html>\n<p>Mueller Tunnel is located about a mile from the summit of <a href=\"https://www.mtwilson.edu/\">Mount Wilson</a>. Itâ€™s easy to park and view <a href=\"https://www.instagram.com/p/5ey73zSf9Z/\">Los Angeles lights</a> at sunset. Then after an easy quarter mile hike you get to the tunnel which can <a href=\"https://www.instagram.com/p/BL7mWWwDd_n/\">capture silloutes wonderfully</a>. If the hike is continued past the tunnel you can reach the summit of Mount Disappointment which offers another view of Los Angeles from mountain tops and has an interesting story behind itâ€™s <a href=\"https://en.wikipedia.org/wiki/Mount_Disappointment_(California)\">name</a>. I walked there this past Sunday, the 15th. Clouds appeared to be surfing the mountain ridge line and all of the rain has created unusual and beautiful waterfalls which are traversing the mountain valleys.</p>\n<h2>ğŸš´ Riding In the Rainâ€”on; board or bike</h2>\n<p>Cycling has continued to be a challenge with the rain however views from the saddle are irresistable. I was able to maintain a bit over <a href=\"https://www.strava.com/athletes/722335#interval?interval=201702&#x26;interval_type=week&#x26;chart_type=miles&#x26;year_offset=0\">130</a> but my hips were a concern going into my third week. I was able to remedy this with a bit of surfing and climbing in the gym. Yeah, gym climbing has been great for me. It challenges me to stretch and relax. </p>\n<html><head></head><body><figure class=\"width--content\">\n  \n</figure></body></html>\n<h2>ğŸ¦„ Nuxt, Next and Svelte</h2>\n<p>Over the past week, I reseached <a href=\"https://nuxtjs.org/\">Nuxt</a>, <a href=\"https://zeit.co/blog/next\">Next</a> and <a href=\"https://svelte.technology/\">Svelte</a>. Iâ€™m currently working on basic setups for Nuxt and Next with a <a href=\"https://github.com/briangonzalez/\">mentor of mine</a>. If it makes sense, those projects will be open sourced. Iâ€™m also planning on using Svelte personally on this here, blog site. Iâ€™m a huge fan of <a href=\"https://github.com/Rich-Harris\">Rich Harris</a>. <a href=\"https://github.com/rollup/rollup\">Rollup</a> is my <em>go-to</em>for open source projects. It keeps my code honest. </p>\n<h2>ğŸ˜ Tried and True</h2>\n<p>I didnâ€™t have a chance to wrap up an open source project that I hoped to release this weekâ€”hopefully next week. Iâ€™m also hoping to write a <a href=\"https://www.npmjs.com/package/jquery-builder\">jQuery builder</a> because the current project seems a bit stale and could maybe be optimized in a few ways. My goal is to make a modern way to build a custom version of jQuery that only uses whatâ€™s needed for a project. Aside from that, Iâ€™ve done some deep learning on <a href=\"https://developers.google.com/web/progressive-web-apps/\">progressive web apps</a> and automatting a <a href=\"http://danielmall.com/articles/how-to-make-a-performance-budget/\">webpage performance report</a>.</p>\n<p>Last and not least, I got my first custom surfboard made by <a href=\"https://www.instagram.com/critterssurfboards/\">Caleb, â€˜Critter Surfboardsâ€™</a>.</p>\n<html><head></head><body><figure class=\"width--content\">\n  \n</figure></body></html>",
